body {
  background-color: black;
}
#head {
  color: white;
  text-align: center;
  margin: 20px;
  padding: 10px;
  border-bottom: 2px solid white;
}

.grid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 15px;
}

.box {
  width: auto;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}

p {
  margin-top: 5%;
  font-size: 28px;
}

.ft {
  background-color: black;
  color: white;
  text-align: center;
  padding: 2%;
  width: auto;
  margin-top: 8%;
}

.ft a {
  color: white;
}

.ft img {
  height: 20px;
  width: 20px;
}

.ft span {
  margin: 6px;
  display: block;
}
@media only screen and (max-width: 450px) {
  span {
    display: block;
  }
}
